* {
  font-family: 'Rubik', sans-serif;
}
body {
  margin: 0;
  padding: 0;
  background-color: #f5f5f5;
}
body #root {
  position: relative;
}
body #root .to-dashboard {
  display: flex;
  align-items: center;
  position: absolute;
  top: 2vh;
  right: 2vw;
  background-color: #fff;
  border-radius: 5px;
  padding: 5px 10px;
}
body #root .to-dashboard p {
  margin: 0;
  user-select: none;
}
body #root .to-dashboard:hover {
  opacity: 0.9;
  box-shadow: 0 0 10px 0 rgba(255,255,255,0.438);
  cursor: pointer;
}
body #root .to-dashboard i {
  margin-right: 5px;
}
body #root .top-background {
  height: 30vh;
  width: 100vw;
}
body #root .top-background img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
body #root .search-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  height: 17vh;
  width: 100vw;
  top: 21.5vh;
}
body #root .search-bar .search-image {
  position: absolute;
  z-index: 2;
  top: -18.5vh;
  right: 40%;
  height: 21.5vh;
  width: 21.5vw;
}
body #root .search-bar .search-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
body #root .search-bar .search-container {
  display: flex;
  z-index: 3;
  justify-content: center;
  box-sizing: border-box;
  width: 60%;
  padding: 5vh 5vw;
  height: 100%;
  background-image: linear-gradient(to right, #06e3ac, #19bdf6);
}
body #root .search-bar .search-container form {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
body #root .search-bar .search-container form .search-input {
  width: 100%;
  height: 100%;
  flex: 3;
  border: none;
  border-radius: 5px 0 0 5px;
  padding: 0 1vw;
  font-size: 0.8rem;
  font-weight: 500;
  color: #000;
  background-color: #f5ffff;
}
body #root .search-bar .search-container form .search-input::placeholder {
  color: #414141;
}
body #root .search-bar .search-container form .search-categories {
  width: 100%;
  height: 100%;
  flex: 1;
  border: none;
  border-left: 0.5px solid #919191;
  border-radius: 0 5px 5px 0;
  padding: 0 1vw;
  font-size: 0.8rem;
  font-weight: 500;
  color: #414141;
  background-color: #f5ffff;
}
body #root .search-bar .search-container form .search-button {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  flex: 1;
  border: none;
  border-radius: 3px;
  margin-left: 1vw;
  padding: 0 0.8vw;
  font-size: 1rem;
  font-weight: bold;
  color: #fff;
  background-color: #2b3739;
  cursor: pointer;
}
body #root .search-bar .search-container form button {
  border: none;
  border-radius: 5px;
  padding: 0 1vw;
  font-size: 1.5rem;
  font-weight: 500;
  color: #fff;
  background-color: transparent;
  cursor: pointer;
}
body #root .pag-band {
  box-sizing: border-box;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  margin-left: 15vw;
  width: 70%;
  background-color: #fff;
  padding: 2vh 1vw;
  border-left: 7px solid #06e3ac;
  font-weight: bold;
  box-shadow: 0 0 5px 0 rgba(0,0,0,0.2);
}
body #root .pag-band .nav-buttons button {
  border-radius: 2px;
  padding: 6px 15px;
  border: none;
  cursor: pointer;
  color: #fff;
  background-color: #06e3ac;
}
body #root .pag-band .nav-buttons button:first-child {
  margin-right: 0.5vw;
}
body #root .pag-band .nav-buttons button[disabled] {
  opacity: 0.5;
  cursor: not-allowed;
}
body #root .products {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  padding: 30px 15vw 0 15vw;
}
body #root .products .p-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
body #root .products .product-card {
  box-sizing: border-box;
  width: 22%;
  margin-right: 1%;
  margin-bottom: 30px;
  background-color: #fff;
  padding: 5px;
  box-shadow: 0 0 3px 0 rgba(0,0,0,0.2);
}
body #root .products .product-card:nth-child(4n),
body #root .products .product-card:last-child {
  margin-right: 0;
}
body #root .products .product-card .info-padding {
  padding: 15px 10px 0 10px;
}
body #root .products .product-card .info-padding .info .p-name {
  font-size: 1.1rem;
  font-weight: bold;
  color: #000;
}
body #root .products .product-card .info-padding .info .p-description {
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 0.8rem;
  font-weight: 500;
  color: #6f6f6f;
}
body #root .products .product-card .info-padding .info .row {
  margin: 10px 0;
}
body #root .products .product-card .info-padding .info .row .p-category {
  font-size: 0.8rem;
  font-weight: bold;
  color: #00c291;
}
body #root .products .product-card .info-padding .info .row .p-price {
  font-size: 0.8rem;
  font-weight: bold;
  color: #000;
  align-items: center;
}
body #root .products .product-card .info-padding .info .row .p-price:before {
  content: '$ ';
  font-size: 0.6rem;
}
body #root .products .product-card .info-padding .product-pub {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
}
body #root .products .product-card .info-padding .product-pub .pub-info {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}
body #root .products .product-card .info-padding .product-pub .pub-info .p-pub-avatar img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 5px;
  object-fit: cover;
}
body #root .products .product-card .info-padding .product-pub .pub-info .p-publisher {
  font-size: 0.7rem;
  font-weight: bold;
  color: #000;
}
body #root .products .product-card .info-padding .product-pub .rating {
  font-size: 0.5rem;
  justify-self: flex-end;
  color: #e0d212;
}
body #root .products .product-card hr {
  border: 1px solid rgba(0,0,0,0.136);
}
body #root .crud {
  padding: 30px 10vw 5vh 10vw;
}
body #root .crud .to-home {
  color: #000;
  text-decoration: none;
}
body #root .crud .crud-actions {
  display: flex;
  flex-flow: row nowrap;
}
body #root .crud .crud-actions .add-product {
  display: flex;
  box-sizing: border-box;
  flex: 1;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: left;
  margin-right: 1vw;
}
body #root .crud .crud-actions .add-product label {
  display: flex;
  flex-flow: column nowrap;
  margin-bottom: 15px;
}
body #root .crud .crud-actions .add-product label input {
  margin-top: 5px;
  width: 80%;
  border-radius: 5px;
  border: none;
  box-shadow: 0 0 5px 0 rgba(0,0,0,0.2);
  padding: 5px;
}
body #root .crud .crud-actions .add-product label input:focus {
  outline: none;
  border: 1px solid #00c291;
}
body #root .crud .crud-actions .add-product .action-button button {
  color: #fff;
  background-color: #00c291;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
}
body #root .crud .crud-actions .add-product .action-button button:hover {
  cursor: pointer;
  opacity: 0.8;
  box-shadow: 0 0 10px 0 rgba(0,0,0,0.2);
}
body #root .crud .crud-actions .products-table {
  flex: 2;
  border-collapse: collapse;
}
body #root .crud .crud-actions .products-table td,
body #root .crud .crud-actions .products-table th {
  border: 1px solid #ddd;
  text-align: center;
}
body #root .crud .crud-actions .products-table th {
  box-sizing: border-box;
  background-color: #00c291;
  text-align: left;
  padding: 12px 8px 12px 8px;
  font-weight: bold;
  color: #fff;
}
body #root .crud .crud-actions .products-table tr:nth-child(even) {
  background-color: #e9e9e9;
}
body #root .crud .crud-actions .products-table .products-table-row td {
  padding: 2px 8px 2px 8px;
  font-size: 0.8rem;
}
body #root .crud .crud-actions .products-table .products-table-row .buttons {
  white-space: nowrap;
}
body #root .crud .crud-actions .products-table .products-table-row .buttons button {
  border: none;
  border-radius: 5px;
  color: #fff;
  padding: 5px 10px;
  cursor: pointer;
}
body #root .crud .crud-actions .products-table .products-table-row .buttons button:hover {
  opacity: 0.8;
  box-shadow: 0 0 10px 0 rgba(0,0,0,0.2);
}
body #root .crud .crud-actions .products-table .products-table-row .buttons button:first-child {
  background-color: #00c291;
  margin-right: 10px;
}
body #root .crud .crud-actions .products-table .products-table-row .buttons button:last-child {
  background-color: #f00;
}
.row {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
}
